// rsc
import DeleteIcon from "@mui/icons-material/Delete";
import { IconButton } from "@mui/material";
import axios from "axios";
import "bootstrap/dist/css/bootstrap.min.css";
import React, { useEffect, useRef, useState } from "react";
import { useMediaQuery } from "react-responsive";
import InfiniteScroll from "react-infinite-scroller";

const VisitList = () => {
  // MediaQuery  ->  
  const isPc = useMediaQuery({
    query: "(min-width:1024px)",
  });

  const isTablet = useMediaQuery({
    query: "(min-width:768px) and (max-width:1023px)",
  });

  const isMobile = useMediaQuery({
    query: "(max-width:767px)",
  });

  let screen_style = { margin: "auto", minWidth: "600px", width: "600px" };

  if (isPc) {
    screen_style = { margin: "auto", minWidth: "600px", width: "600px" };
  } else if (isTablet) {
    screen_style = { margin: "auto", minWidth: "600px", width: "90%" };
  } else if (isMobile) {
    screen_style = { margin: "auto", minWidth: "400px", width: "90%" };
  }

  // CORS ->  local      ?    .   . 
  //axios.defaults.withCredentials = true;

  //  :  ReRendering 
  // useState -> (data)        
  const [list, setList] = useState([]);

  //     => useRef import
  const total_count = useRef(0);  //   
  const count = useRef(3);  //    
  const isLast = useRef(false);  //   ? 

  //    
  const handleLoadMore = () => {
    //console.log("---handleLoadMore()---");
    count.current = count.current + 3;

    if (count.current >= total_count.current) { // 
      isLast.current = true;// setLast(true); // true .
    } else {
      isLast.current = false;//setLast(false);
    }
    getData();

  };

  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };


  useEffect(() => {
    // Data  
    // console.log("--useEffect()--");
    getData();
  }, []); //     ->      

  function getData() {
    // server    url..?
    const url = "http://localhost:8080/rest/visits/" + count.current;

    // GET 
    axios
      .get(url)
      .then(function (response) {
        console.log(response.data);

        //useState() -> ReRendering
        setList(response.data.list);

        //    ->          
        total_count.current = response.data.total_count;

      })
      .catch(function (error) {
        console.log(error);
      });
  }

  //   
  function deleteVisit(idx) {
    const c_pwd = prompt(" :");

    //
    if (c_pwd == null) return;

    //   
    if (c_pwd === "") {
      alert("   ");
      return;
    }

    // GET (select) :   
    const url = `http://localhost:8080/rest/check-pwd/${idx}/${c_pwd}`;
    axios
      .get(url)
      .then(function (response) {
        console.log(response);
        //   = true or false
        if (response.data.result === false) {
          alert("   .");
          return;
        }

        //  
        if (window.confirm(" ?") === false) return;

        //    
        deleteVisitData(idx);
      })
      .catch(function (error) {
        console.log(error);
      });
  } // end : deleteVisit()

  //    -> idx  
  function deleteVisitData(idx) {
    const url = "http://localhost:8080/rest/visit/" + idx;
    // DELETE (delete)
    axios
      .delete(url)
      .then(function (response) {
        console.log(response);
        if (response.data.result == false) {
          alert(" ");
          return;
        }

        //      
        getData();
      })

      .catch(function (error) {
        console.log(error);
      });
  } // end: deleteVisitData();

  return (

    <InfiniteScroll
      className="h-[85%] smartPhone:h-[70%]  scrollbar-hide"
      pageStart={0} //   
      loadMore={getData}
    >

      <main>
        {/* <div className="flex flex-col items-center justify-center h-full"> */}

        <div className="flex flex-col h-full" style={screen_style}>
          {list &&
            list.map((v, index) => (
              <div
                key={index}
                style={{
                  border: "1px solid black",
                  margin: "10px",
                  padding: "20px",
                  boxShadow: "1px 1px 3px black",
                }}
              >
                {/*  */}
                <div className="flex flex-row">
                  <span
                    style={{
                      display: "inline-block",
                      width: "80%",
                      textAlign: "left",
                      color: "black",
                      fontWeight: "bold",
                    }}
                  >
                     {v.name}({v.pwd})
                  </span>
                  <span
                    style={{
                      display: "inline-block",
                      width: "20%",
                      textAlign: "right",
                    }}
                  >
                    <IconButton
                      aria-label="delete"
                      id={v.idx}
                      onClick={() => deleteVisit(v.idx)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </span>
                </div>
                {/*  */}
                <p
                  style={{
                    textAlign: "left",
                    color: "gray",
                    whiteSpace: "pre-wrap",
                  }}
                >
                  {v.content.replaceAll("<br>", "\n")}
                </p>

                {/*  */}
                <p style={{ textAlign: "left", color: "#ccc" }}>{v.regdate}</p>
              </div>
            ))}

          {/*    */}
          {!isLast.current && (
            <button
              onClick={handleLoadMore}
              className="btn btn-success mybtn"
            >
              ...
            </button>
          )}


          {isLast.current && (
            <button
              onClick={scrollToTop}
              className="btn btn-success mybtn"
            >
              ...
            </button>
          )}
        </div>
      </main>
    </InfiniteScroll>
  );
};

export default VisitList;
